	module.exports = {"data":{"site":{"siteMetadata":{"docSections":{"basics":["/basics/introduction/"],"guides":["/guides/quick-start-guide/"],"configurations":["/configurations/overview/","/configurations/details/","/configurations/custom-webpack-config/"],"testing":["/testing/manual-testing/"]}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/404/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/configurations/overview/"},"frontmatter":{"title":"Configuration overview","id":"overview"}}},{"node":{"fields":{"slug":"/testing/manual-testing/"},"frontmatter":{"title":"Manual Testing","id":"manual-testing"}}},{"node":{"fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/guides/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}},{"node":{"fields":{"slug":"/configurations/custom-webpack-config/"},"frontmatter":{"title":"Custom webpack configuration","id":"custom-webpack-config"}}},{"node":{"fields":{"slug":"/configurations/details/"},"frontmatter":{"title":"Detailed configuration overview","id":"details"}}}]},"markdownRemark":{"html":"<p>Get started using the automated command line tool. This command creates a Wingsuit demo project.\nThe demo page uses the atomic design principle to structure the patterns.</p>\n<pre><code>npx @wingsuit-designsystem/cli init\n</code></pre>\n<p>After installation the tool opens Storybook and you can browse through the wingsuit demo page.</p>\n<h1 id=\"screenshot\"><a href=\"#screenshot\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>SCREENSHOT</h1>\n<h2 id=\"get-an-overview\"><a href=\"#get-an-overview\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Get an overview</h2>\n<p>A good starting point is to checkout the design system components.\nThe design system components located at <code>source/patterns/default</code>. Additional storybook presentation templates located at  <code>apps/storybook/patterns</code>.</p>\n<p>A typical component includes a <code>pattern.wingsuit.yml</code>, a <code>twig template</code>. Optional a <code>css file</code> and/or a <code>behavior javascript</code> file. </p>\n<h3 id=\"create-a-new-component\"><a href=\"#create-a-new-component\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Create a new component</h3>\n<p>Another good starting point is to create a new component:</p>\n<p><code>yarn ws generate-component</code></p>\n<p>The wizzard will guide you through all possible pattern types including documentation.</p>","fields":{"slug":"/guides/quick-start-guide/"},"frontmatter":{"title":"Quick Start Guide","id":"quick-start-guide"}}},"pathContext":{"slug":"/guides/quick-start-guide/"}}

